.container{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container ul{
    list-style: none;
    display: flex;
}
.container li{
    width: 150px;
    height: 80px;
    padding: 20px;
    margin: 20px 0;
    text-align: center;
    line-height: 40px;
}

.container li:first-child{
    border-right: 1px solid #3f3f3f

}

.container li .HostButton{
    display: inline-block;
    width: 110px;
    height: 40px;
    text-decoration: none;
    border-radius: 20px;
    background-color: #888;
    color: #fff;
}
.container li .HostButton:hover{
    background-color: #333;
}

.container li .clientButton{
    display: inline-block;
    width: 110px;
    height: 40px;
    text-decoration: none;
    border-radius: 20px;
    border: 1px solid #3f3f3f;
    color: #0c0c0c;
}
.container li .clientButton:hover{
    background-color: #888;
}